<template>
	<div class="row mt-3">
		<div class="col-md-12 col-lg-12">
			<div class="card">
				<div class="card-body">
					<img class="img-fluid center-block mx-auto d-block" src="https://cn.vuejs.org/images/logo.png">
					<form @submit.prevent="onSubmit">
						<div class="form-group">
							<label  for="sn" >用户名</label>
							<input class="form-control" 
							:class="{ 'is-valid':Issuccess,'is-invalid':Isdanger}"
							@blur="checkSaleName"
							v-model="salename" 
							placeholder="请输入用户名" 
							type="text" id="sn">
							<div :class="[ classSelect ]" v-show="Isdanger||Issuccess">
					          {{ altermsg }}
					        </div>
							<label class="alert alert-warning" v-if="nIsEmpty" for="password">{{ nAlert }}</label>
						</div>

						<div class="form-group">
							<label for="ps">密码</label>
							<input class="form-control" 

							@blur="checkPassWord" 
							v-model="password" 
							placeholder="请输密码" 
							type="password" id="ps">
							<label class="alert alert-warning" v-if="psIsEmpty" for="password">{{ psAlert }}</label>
						</div>
						<button type="submit" class="btn btn-block btn-success">登录</button>
					</form> 
				</div>
			</div>
		</div>
	</div>
</template>
<script>
	// import axios from 'axios'
	export default{
		data(){
			return {
				salename:'',
				password:'',
				nIsEmpty:false,
				psIsEmpty:false,
				nAlert:'账号不能为空哦',
				psAlert:'密码不能为空哦',
				Issuccess:false,
				Isdanger:false,
				classSelect:'',
				altermsg:''
			}
		},
		methods:{
			onSubmit(){
				if (this.Issuccess&&this.salename!=''&&this.password!='') {
					let formData={
						salename : this.salename,
						password : this.password
					};
					this.$axios.post('http://localhost:8080/WaterWarm1.0/SaleLogin',formData)
					.then(res=> {
						let resultjson=res.data;
						console.log(resultjson);
						if (resultjson.code==200) {
							window.alert('登录成功！');
							this.salename='';
							this.password='';
							this.nIsEmpty=false;
							this.psIsEmpty=false;
							this.Issuccess=false;
							this.Isdanger=false;
						}else if (resultjson.code==303) {
							window.alert('登录失败，密码错误！');
						}else if (resultjson.code==404) {
							window.alert('登录失败,编辑框内容不能为空！');
						}else {
							window.alert('登录失败,未知错误,请联系管理员！');
						}
					})
				}else{
					window.alert('表单数据不合法,请更正后重试！');
				}
			},
			checkSaleName(){
				this.nIsEmpty=this.salename===''?true:false;
				//请求数据库进行检测账号是否存在
				// http://localhost:8080/WaterWarm1.0/SaleNameCheck
				let formData={
					salename : this.salename,
					password : this.password
				};
				this.$axios.post('http://localhost:8080/WaterWarm1.0/SaleNameCheck',formData)
				.then(res=> {
					let resultjson=res.data;
					console.log(resultjson);
					if (resultjson.msg=="账号可用") {
						this.Isdanger=true;
						this.Issuccess=false;
						this.classSelect='invalid-feedback';
						this.altermsg="此账号可能还不存在";
					}else if (resultjson.msg=="账号已存在") {
						this.Issuccess=true;
						this.Isdanger=false;
						this.classSelect='valid-feedback';
						this.altermsg="这个是本网站的老用户了哦，快输入密码登录吧";
					}else{
						this.Issuccess=false;
						this.Isdanger=false;
					}
				})
			},
			checkPassWord(){
				this.psIsEmpty=this.password===''?true:false;
				
			}
		}
	}

</script>


